  }
  .introduce {
    letter-spacing: 1px;
    padding: 15px;
    p {
      text-align: center;
      color: #777;
      font-size: 12px;
      letter-spacing: 1px;
    }
    .things {
      padding: 20px;
      text-align: left;
      h4 {
